
.em {
  display: inline-block;
  width: 1em;
}
.em_5 {
  display: inline-block;
  width: .5em;
}
body,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dl,
dt,
dd,
input,
form {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
body {
  background-color: #f0eff6;
  color: #372f2c;
  font-family: "Microsoft Yahei";
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#devicenum .header {
  padding: 10px 20px;
}
#devicenum .info {
  background: #fff;
  padding: 20px;
}
#devicenum .info .tit {
  border-bottom: 1px solid #ddd;
}
#devicenum .info .tit .diff {
  overflow: hidden;
  margin: 20px 0;
}
#devicenum .info .tit .diff li {
  float: left;
  width: 33%;
  overflow: hidden;
}
#devicenum .info .tit .diff li .fl {
  width: 40px;
  height: 25px;
  border-radius: 5px;
}
#devicenum .info .tit .diff li .mr {
  margin-right: 15px;
}
#devicenum .info .tit .diff .rged .k1 {
  background: #999;
}
#devicenum .info .tit .diff .cose .k2 {
  border: 1px solid #999;
}
#devicenum .info .tit .diff .cked .k3 {
  background: #04b101;
}
#devicenum .info .num {
  overflow: hidden;
}
#devicenum .info .num li {
  float: left;
  width: 22%;
  height: 55px;
  text-align: center;
  line-height: 55px;
  margin-right: 2.8%;
  margin-top: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  color: #04b101;
}
#devicenum .info .num .mr0 {
  margin-right: 0;
}
#devicenum .info .num .active {
  color: #fff;
  background: #04b101;
}
#devicenum .smit {
  margin-top: 20px;
  width: 85%;
  margin-left: auto;
  margin-right: auto;
  background: #04b101;
  color: #fff;
  border-radius: 10px;
  text-align: center;
  padding: 20px 0;
}



